const ProceduralGeneration = () => {
    const proceduralGenerationUnityContext = new UnityContext({
        loaderUrl:"games/ProceduralGeneration.loader.js",
        dataUrl:"games/ProceduralGeneration.data",
        frameworkUrl:"games/ProceduralGeneration.framework.js",
        codeUrl: "games/ProceduralGeneration.wasm",
    })
    return(
        <div className="font-medium px-2">
            <h2 className="text-3xl px-2 py-2">Procedural generation</h2>
            <p className="px-3 py-3">
            W A S D to move camera.
            <br /> Click to generate new seed
            </p>
            <Unity className="mx-auto" unityContext={proceduralGenerationUnityContext} width="100%" height="600px"  />
            <p className=" py-3">
                This project is my hobby exploration project into procedural generation.
                <br />
            </p>
            <p>
                The project started out as a small exploration project already in 2020 and it has been developed since in a very on and off fashion.
                <br /><br />
                The first island implementation was done using heightmap based generation.

            </p>
            <img src={ProceduralGeneration1Image} className="sd:w-1 md:w-3/4 mx-auto py-4 shadow-xl" alt="Picture of a heightmap based implementation."/>
            <img src={ProceduralGeneration3Image} className="sd:w-1 md:w-3/4 mx-auto py-4 shadow-xl" alt="Heightmap based autumn colored island."/>
            <p>
                The latest implementation has been done by using voxels and marching cubes.
            </p>
            <img src={ProceduralGeneration2Image} className="sd:w-1 md:w-3/4 mx-auto py-4 shadow-xl" alt="Picture of a voxel based implementation."/>
            <p>
                The project has involved other experimentation as well, like this terribly unoptimised geometry shader based grass. (looks pretty, runs horrid)
            </p>
            <img src={ProceduralGeneration4Image} className="sd:w-1 md:w-3/4 mx-auto py-4 shadow-xl" alt="Picture of geometry shader based grass."/>
        </div>)
}



